import React from 'react';
import {createNativeStackNavigator} from '@react-navigation/native-stack';
import {createBottomTabNavigator} from '@react-navigation/bottom-tabs';
import { CssView } from '@base/ui-kit/components/BaseComponent';
import { SvgSource } from './assets/SvgSource';
import { Colors } from './themes/colors';
import {HomeScreen} from "./screen/HomeScreen";


const Stack = createNativeStackNavigator();
const Tab = createBottomTabNavigator();

const _route: any = {
    "My Assets": <CssView width={24} height={24}><SvgSource.iconExpense /></CssView>,
    "Requests": <CssView width={24} height={24}><SvgSource.iconExpense /></CssView>,
    "Work Orders": <CssView width={24} height={24}><SvgSource.iconExpense /></CssView>,
}

const _routeActive: any = {
    "My Assets": <CssView width={24} height={24}><SvgSource.iconExpenseActive /></CssView>,
    "Requests": <CssView width={24} height={24}><SvgSource.iconExpenseActive /></CssView>,
    "Work Orders": <CssView width={24} height={24}><SvgSource.iconExpenseActive /></CssView>
}

const BottomTabs = () => {
    return (
        <Tab.Navigator
            screenOptions={({route}) => ({
                tabBarActiveTintColor: Colors.primary,
                tabBarInactiveTintColor: Colors.grey3,
                headerShown: false,
                tabBarIcon: ({focused}) => {
                    if (focused) {
                        return _routeActive[route.name]
                    }
                    return _route[route.name]
                }
            })}>
            <Tab.Screen name="My Assets" component={HomeScreen}/>
            <Tab.Screen name="Requests" component={HomeScreen}/>
            <Tab.Screen name="Work Orders" component={HomeScreen}/>
        </Tab.Navigator>
    );
};

const Main = () => {
    return (
        <Stack.Navigator screenOptions={{
            headerShown: false
        }}>
            <Stack.Screen name="BottomTabs" component={BottomTabs} />
        </Stack.Navigator>
    );
};

export default Main;

